.AboutMe__container{
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    column-gap: 4rem;

    max-width: 968px;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    gap: 1.5rem;
    margin-bottom: 4rem;

    
    .AboutMe__img{
        width: 350px;
        border-radius: 1.5rem;
        justify-self: center;
    }
    .AboutMe__data{

    .AboutMe__description{
        padding: 0 10rem 0 0;
        margin-bottom: 2rem;

        
    }

    }

}

.my-button{
    background-image: linear-gradient(to right, #F2994A 0%, #F2C94C  51%, #F2994A  100%);
    margin: 10px;
    padding: 15px 45px;
    text-align: center;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;           
    border-radius: 10px;
    font-family: Trebuchet MS;
    font-size: 20px;
    font-weight: 800;
    font-style: normal;
    text-decoration: none;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-shadow: 10px 7px 24px -2px rgba(202.7984619140625,1.3357242789588304,1.3357242789588304,0.5);
   }
   a.my-button span{
    margin-left:22px;
    display: block;
    width: 40px;
    height: 40px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: contain;
    transform: rotate(0deg);
   }
   a.my-button:hover{
      background-position: right center; /* change the direction of the change here */
      color: #fff;
      text-decoration: none;
   }
   a.my-button:active{
    transform: scale(0.95);
   }         

   